import React, { createRef } from "react"
import { useState } from "react"
import { useEffect } from "react"

const utterancSettings = {
  src: "https://utteranc.es/client.js",
  repo: "jeonghwan-kim/jeonghwan-kim.github.com",
  "issue-term": "pathname",
  label: "Comment",
  theme: "github-light",
  crossOrigin: "anonymous",
  async: "true",
}

let timerId = null

const PostComment: React.FC = () => {
  const ref = createRef<HTMLDivElement>()
  const [loaded, setLoaded] = useState(false)

  useEffect(() => {
    const utterances = document.createElement("script")
    Object.entries(utterancSettings).forEach(([key, value]) => {
      utterances.setAttribute(key, value)
    })
    ref.current.appendChild(utterances)

    timerId = setInterval(() => {
      console.log("setInterval", ref.current?.childElementCount)
      if (ref.current?.childElementCount > 0) {
        setLoaded(true)
      }
    }, 1000)
  }, [])

  return (
    <>
      <div id="utteranc-comments" ref={ref}></div>
      {!loaded && <div>Loading...</div>}
    </>
  )
}

export default PostComment
